.App {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100vw;
  min-height: 100vh;
}

.Container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0 10px;
  text-align: center;
}

/* üñºÔ∏è Imagen m√°s peque√±a */
img {
  max-width: 150px;
  height: auto;
  object-fit: cover;
}

/* üîò Bot√≥n m√°s peque√±o */
button {
  background-color: crimson;
  color: #fff;
  margin-top: 8px;
  padding: 6px 20px;
  border: none;
  border-radius: 20px;
  cursor: pointer;
  font-size: 0.8rem;
  transition: transform 0.2s ease, background-color 0.2s ease;
}

button:hover {
  background-color: #b30f2b;
  transform: scale(1.05);
}

.gap-1 {
  gap: 0.5em;
}

.flex {
  display: flex;
}

.w-full {
  width: 100%;
}

.align-items-center {
  align-items: center;
}

.justify-content-center {
  justify-content: center;
}

.grid {
  display: grid;
}

/* üïí Temporizador peque√±o */
.timer {
  font-size: clamp(14px, 3vw, 22px);
  font-weight: bold;
  color: #222;
  text-align: center;
}

/* ‚è∞ Mensaje a√∫n m√°s chico */
.timeout-message {
  font-size: clamp(12px, 3vw, 18px);
  color: rgb(0, 0, 0);
  text-align: center;
  white-space: nowrap;
}
